<template>

        <!--Design para mobile, tablets, ipads, computadores e outros-->
        <v-container fluid :class="{'pt-1': $vuetify.breakpoint.mdAndUp }">

            <v-layout>

                <!--Nav-bar fixo esquerdo que esconde nos dispositivos moveis-->
                <v-flex xs2 offset-xs2 class="hidden-md-and-down">
                    <v-layout class="explicador-bar-fixed">
                        <v-flex xs2 offset-xs2>
                            <v-layout row>

                                <!--Apresentacao do explicador aqui.-->
                                <v-layout>
                                    <v-flex xs12 class="white elevation-5  pb-">

                                        <!--Informacao do meu perfil-->
                                        <v-layout v-if="isMyProfile">
                                            <v-flex xs12 class="white--text green text-xs-center">

                                                <v-layout>
                                                    <!--Nome do explicador-->
                                                    <v-flex xs12 class="ma-0 pa-0">
                                                        <!--Nome do utilizador-->
                                                        <h3>Meu Perfil</h3>

                                                    </v-flex>

                                                </v-layout>

                                            </v-flex>
                                        </v-layout>

                                        <v-divider></v-divider>

                                        <!--Imagem-->
                                        <v-layout class="mt-2 pa-2">
                                            <!--Foto-->
                                            <v-flex xs3>
                                                <!--Foto de utilizador-->
                                                <img :src="img" class="img-radius"/>

                                            </v-flex>

                                            <!--Nome do explicador-->
                                            <v-flex xs9 class="ml-2 orange--text">

                                                <!--Nome do utilizador-->
                                                <h2>{{ nomeUser }}</h2>

                                            </v-flex>

                                        </v-layout>

                                        <v-divider class=""></v-divider>

                                        <!--Alguns dados academicos-->
                                        <v-layout>

                                            <v-flex class="mt-3 pa-2">
                                                <!--Instituicao do utilizador-->
                                                <h4 class="pb-2 blue--text"><v-icon>school</v-icon> {{ userInstitution }}</h4>
                                                <h4 class="pb-2 grey--text"><v-icon>school</v-icon> {{ userCourse }}</h4>
                                                <h4 class="pb-2 green--text"><v-icon>school</v-icon> {{ userDepartment }}</h4>

                                            </v-flex>

                                        </v-layout>
                                    </v-flex>
                                </v-layout>

                            </v-layout>
                            <v-layout>
                                <v-flex>

                                    <v-btn block color="white" router @click="scrollTo(480)" class="azul-principal--text">Disciplinas</v-btn>

                                    <!--&lt;!&ndash;Btn contacto do explicador&ndash;&gt;-->
                                    <!--<v-btn if="!isMyProfile" class="white&#45;&#45;text" color='blue' @click="scrollTo('#contact')" block="" title="Ver os contactos do explicador">-->
                                        <!--<v-icon left dark>phone</v-icon>-->
                                        <!--Ver Contactos-->
                                    <!--</v-btn>-->

                                    <v-btn block color="white" router @click="scrollTo('#dadosPessoais')" class="azul-principal--text">Informações pessoais</v-btn>

                                    <v-btn v-if="isMyProfile" block color="white" router @click="scrollTo('#fotoDePerfil')" class="azul-principal--text">Imagem do Perfil</v-btn>

                                    <v-btn block color="white" router @click="scrollTo('#dadosAcademicos')" class="azul-principal--text">Informações Académicas</v-btn>

                                    <v-btn block color="white" router @click="scrollTo('#localizacao')" class="azul-principal--text">Localizacao</v-btn>

                                </v-flex>
                            </v-layout>

                            <!--<v-btn round outline block router >Testemunhos</v-btn>-->

                        </v-flex>
                    </v-layout>
                </v-flex>

                <!--Div que coloca espaco no dispositivo medio-->
                <v-flex md1 v-if="$vuetify.breakpoint.mdAndDown"></v-flex>

                <!--Informacao central nao fixa-->
                <v-flex  xs12 md10 lg6 class="mb-3" :class="{ 'ml-2': $vuetify.breakpoint.mdAndUp }" >

                    <!--Corpo-->
                    <v-card-text class="ma-0 pa-0 lighten-5 text-xs-center">

                        <!--Cabecalho de cima.-->
                        <template>
                            <!--Card Azul com a foto-->
                            <v-layout class="red ma-0">
                                <v-flex xs12 class="blue" style="height: 220px">

                                    <h3 v-if="isMyProfile" style="position: absolute;margin: 20px 10px"
                                        class="white--text">Meu perfil</h3>

                                    <explicador-show-contact-dialog
                                        v-else-if="user"
                                        :user="user"
                                        :onExplicadorTopProfile="true"></explicador-show-contact-dialog>

                                    <v-card width="100px"
                                        :style="isMyProfile ? 'margin: 170px auto' : 'margin: 150px auto'">
                                        <v-card-media :src="img" height="100px">
                                        </v-card-media>

                                        <!--Botao para editar a foto de perfil-->
                                        <v-btn
                                            v-if="isMyProfile"
                                            class="edit-photo-icon"
                                            color="orange"
                                            icon
                                            title="Alterar foto de perfil"
                                            @click.prevent.stop="showDialog = !showDialog">
                                            <v-icon>edit</v-icon>
                                        </v-btn>
                                        <profile-photo-upload v-show="false" :user="user" @closing="fechandoDialog" :showDialog="showDialog"></profile-photo-upload>
                                    </v-card>


                                </v-flex>


                            </v-layout>

                            <!--Card de baixo-->
                            <v-layout class="red ma-0">

                                <v-flex xs12 class="white" style="height: 230px">

                                    <div style="margin-top: 80px"></div>
                                    <h3> {{ nomeUser }}</h3>
                                    <h4> {{ userCourseOrClass }}</h4>
                                    <h4> {{ userInstitution }}</h4>
                                    <h4> {{ userLocation }} </h4>

                                </v-flex>

                            </v-layout>

                            <!--Total disciplinas-->
                            <v-layout class="ma-0">

                                <v-flex xs12>

                                    {{ totalSubjects }} disciplinas

                                </v-flex>

                            </v-layout>
                        </template>

                        <!--Disciplinas-->
                        <v-layout id="disciplinas" class="mt-2" justify-center>

                            <mobile-profile-subjects-info :user="user" :circularSize="circularSize"></mobile-profile-subjects-info>

                        </v-layout>

                        <!--Dados Pessoais-->
                        <v-layout id="dadosPessoais" class="mt-2" justify-center>

                            <mobile-profile-personal-info :user="user" :circularSize="circularSize"></mobile-profile-personal-info>

                        </v-layout>

                        <!--Dados Academicos-->
                        <v-layout id="dadosAcademicos" class="mt-2" justify-center>

                            <mobile-profile-academic-info :user="user" :circularSize="circularSize"></mobile-profile-academic-info>

                        </v-layout>

                        <!--Localizacao-->
                        <v-layout id="localizacao" class="mt-2" justify-center>

                            <mobile-profile-location-info :user="user" :circularSize="circularSize"></mobile-profile-location-info>

                        </v-layout>

                        <v-layout class="mt-2 elevation-5 hidden-md-and-up" >
                            <v-flex xs12>
                                <p class="mt-2" style="font-size: 8pt;">
                                    Para ver o perfil completo acesse pelo computador</p>
                                <p><a href="https://explicador.co.mz">explicador.co.mz</a></p>
                            </v-flex>
                        </v-layout>
                    </v-card-text>

                </v-flex>

            </v-layout>


        </v-container>


</template>

<script>
    export default {
        props: ['user', 'name'],
        data: () => ({
            hidden: false,
            nameU: '',
            showDialog: false,
            isThis: false,
            circularSize: 40,
        }),
        created () {
            this.scrollTo(0)
            if (this.isConfirmMailSent)
            {
                setTimeout(this.mostrarDialog(), 150000);

            }

        },
        computed: {
            options () { //usado para fazer o scroll
                return {
                    duration: 1000,
                    offset: 0,
                    easing: 'easeInQuint',
                    controlRoute: false,
                }
            },
            isUser () {
                return this.user !== undefined && this.user !== null
            },
            img () {
                return this.isUser ? this.user.photoURL : '//ssl.gstatic.com/s2/oz/images/sge/grey_silhouette.png'
            },
            nomeUser() {
                return this.isUser && this.user.name !== null && this.user.name !== '' ? this.user.name : 'Sem Nome'
            },
            totalSubjects ()
            {
                let subjects = this.isUser && this.user.Explicador && this.user.Explicador.subjectDetails ? this.user.Explicador.subjectDetails : []
                let total = 0
                subjects.forEach((sbs) => {
                    total += sbs.subjects.length
                })
                return total
            },
            userLocation() {

                const negative = 'Ainda nao registou a localizacao'

                if (!this.isUser || !this.user.Locations) return negative

                let userBairro = this.user.Locations.bairro
                let isFromMaputoCity = this.user.Locations.isFromMaputoCity
                let isFromMaputoProvince = this.user.Locations.isFromMaputoProvince
                let district = this.user.Locations.district
                let locality = this.user.Locations.locality

                return (isFromMaputoCity || isFromMaputoProvince) && userBairro !== null && userBairro
                        ? userBairro : (locality)
                        ? locality : district
                        ? district : negative

            },
            userInstitution () {

                const defaultTitle = 'Sem Instituicao'

                if (!this.isUser || !this.user.Educations) return defaultTitle

                let userEd = this.user.Educations

                let inst = userEd.level === 'Primário' ? userEd.userSchool : defaultTitle
                inst = userEd.level === 'Secundário' ? userEd.userSchool : inst
                inst = userEd.level === 'Técnico' ? userEd.institution : inst
                inst = userEd.level === 'Superior' ? userEd.institution : inst
                inst = userEd.level === 'Outro' ? userEd.institution : inst

                return inst
            },
            userCourseOrClass () {

                const defaultTitle = 'Sem Faculdade'

                if (!this.isUser || !this.user.Educations) return defaultTitle

                let userEd = this.user.Educations

                let inst = userEd.level === 'Primário' ? userEd.userClass : defaultTitle
                inst = userEd.level === 'Secundário' ? userEd.userClass : inst
                inst = userEd.level === 'Técnico' ? userEd.courseNivel : inst
                inst = userEd.level === 'Superior' ? userEd.courseNivel : inst
                inst = userEd.level === 'Outro' ? userEd.userSchool : inst

                return inst
            },
            userAuth () {
                return this.$store.getters.userAuth
            },


            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            isMyProfile () {
                return this.isUserAuth && this.isUser && this.user.id === this.userAuth.id
            },
            userVerified () {
                return !this.user.emailVerified
            },
            gender () {

                const negative = 'Sexo nao definido'

                if (!this.isUser) return negative

                let genero = this.user.gender

                return genero && genero !== null && genero && genero !== '' ? genero : negative


            },
            userExplainSome () {

                if (!this.isUser) return false

                return this.user.Explicador && this.user.Explicador.explainLevels
            },

            userFaculty () {

                const negative = 'Sem Faculdade'

                if (!this.isUser) return negative

                let ed = this.user.Educations
                return ed && ed.faculty ?  'Faculdade de ' + ed.faculty : negative
            },
            userCourse () {

                const negative = 'Sem Curso'

                if (!this.isUser) return negative

                let ed = this.user.Educations

                return ed && ed.course ? 'Curso de ' + ed.course : negative

            },
            userDepartment () {

                const negative = 'Sem Departamento'

                if (!this.isUser) return negative

                let ed = this.user.Educations

                return ed && ed.department ? ed.department : negative

            },
            userNivel () {

                const negative = 'Sem Nivel'

                if (!this.isUser) return negative

                let ed = this.user.Educations

                return ed && ed.nivel ? ed.nivel : negative


            },
            hasSubjects () {

                if ( !this.isUser ) return false

                return this.user && this.user.Explicador && this.user.Explicador.subjects
            },

            userSubjects () {

                const negative = []

                if (!this.isUser) return negative

                return this.user.Educations && this.user.Educations.subjects ? this.user.Educations.subjects : []

            },

            hasSomeLocation () {

                if ( !this.isUser ) return false

                return this.user.Location

            },

            userProvince () {

                const negative = 'Provincia Não registada'

                if (!this.hasSomeLocation) return negative

                let userProv = this.user.Location.province

                return userProv !== null && userProv !== undefined && userProv !== '' ? userProv : negative

            },

            userCity () {

                const negative = 'Cidade Não registada'

                if (!this.hasSomeLocation) return negative

                let userCity = this.user.Location.city

                return userCity !== null && userCity !== undefined && userCity !== '' ? userCity : negative

            },

            route() {
                return this.$route.path
            },
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },

            isConfirmMailSent () {
                return this.$store.getters.getConfirmMailSent
            },

        },
        watch: {

            route(value) {

                window.location.reload(false);

            },
            loading (value) {
                if (!value && !this.error && this.isThis) {

                    this.mostrarDialog()
                    this.isThis = false

                }
            }

        },
        methods: {

            scrollTo (el) {

                if (isNaN(el))
                {
//                    Scroll to a certain element
                    document.querySelector(el).scrollIntoView({
                        behavior: 'smooth'
                    });
                }
                else
                {
                    window.scroll({
                        top: el,
                        left: 10,
                        behavior: 'smooth'
                    });
                }




            },

            fechandoDialog ()
            {
                this.showDialog = false
            },

            sendVerificationLink () {
                this.isThis = true
                this.$store.dispatch('sendVerificationLink')
            },
            mostrarDialog () {

                const title = 'Email de confirmação enviado com sucesso .. .'
                const message = 'Acabamos de te enviar um email. Acesse a caixa de entrada na sua conta de email para confirmar. Te enviamos um link de confirmação, e só clicar. Cumprimentos.'
                const btn1 = ''
                const btn2 = 'Ok'

                /**
                 * Primeiro param: true: significa abrir a dialog
                 * Segundo param:  title: o titulo a mostrar
                 * Terceiro param:  msg: a mensagem a mostrar
                 */
                this.$store.dispatch('togleConfirmDialog', {show: true, title: title, message: message,btn1: btn1,btn2: btn2})
                this.$store.dispatch('confirmationMailSet', false) //mostra que o email de confirmacao ja foi enviado

            },

        },
    }
</script>
<style>

    .edit-photo-icon {
        top: -20px;
        right: -20px;
        position: absolute;
    }

    .explicador-bar-fixed {
        z-index: 0;
    }
    .explicador-edit-item {
        float: right;
        cursor: pointer
    }
    .icon {
        font-size: 18px;
    }
    .img-radius {
        width: 50px;
        height: 50px!important;
    }
    .container {
        padding: 0px;
    }

    .elevation-10 {
        /*box-shadow: 0 6px 6px -3px rgba(0,0,0,.2),0 10px 14px 1px rgba(255,9,5,.6),0 4px 1px 3px rgba(0,0,0,.12)!important;*/
    }

</style>


